import React from "react";
import CardProject from "./CardProject";
import { ListProjects } from "../model/Projects";
import { useTranslation } from "react-i18next";
import { Fade } from "react-awesome-reveal";


const CardProjects = ()=> {
    const { t } = useTranslation();
    return (
    <div className="w-full text-start">
        <div className="bg-white w-full 2xl:max-w-screen-2xl xl:max-w-screen-xl md:max-w-screen-sm lgC:max-w-screen-lg-custom mdC:max-w-screen-md-custom justify-between mx-auto items-center rounded-lg border-4 border-neutral-700/50 dark:bg-gray-800 dark:border-neutral-300/50 dark:text-white shadow-md">
            <div className="flex p-2 gap-1 border-b-4 border-b-neutral-500/50 ">
                <div className="">
                    <span className="bg-red-500 inline-block center w-3 h-3 rounded-full"></span>
                </div>
                <div className="circle">
                    <span className="bg-yellow-500 inline-block center w-3 h-3 rounded-full"></span>
                </div>
                <div className="circle">
                    <span className="bg-green-500 box inline-block center w-3 h-3 rounded-full"></span>
                </div>
                <div className="mx-auto font-bold text-xl">{t('projects.title')}</div>
            </div>
            <div className="card__content flex flex-wrap">
            <Fade cascade={true} damping={0.1} direction="up" triggerOnce={true}>
            {Object.entries(ListProjects).map(([key, { url, alt, img, text, textLink, css, classCustom,height,width }], index) => (
                    <div key={key} className="gap-5 m-4 w-full">
                        <CardProject url={url} img={img} text={text} alt={alt} textLink={textLink}  css={css} classCustom={classCustom} height={height}  width={width}/>
                    </div>
                ))}
            </Fade>
                <div className="flex justify-center items-center text-center mt-8 w-full">
                    <p className="font-Onest text-gray-600 text-sm [&>strong]:text-red-800 dark:text-white dark:[&>strong]:text-red-300 m-4 md:mb-2">
                        <strong>{t('projects.note.text1')}</strong>{t('projects.note.text2')}
                    </p>
                </div>
            </div>
        </div>
    </div>
    )
};

export default CardProjects