import React, { useState } from "react";
import { Link } from "react-scroll";
import { useTranslation } from "react-i18next";
import SwitchSpot from "./Switch";


export const LANGUAGES = [
    { label: " Espaol", code: "es" },
    { label: " English", code: "en" }
  ];
  

const Header = ()=> {
    const { i18n, t } = useTranslation();
    const onChangeLang = (e: React.ChangeEvent<HTMLSelectElement>) => {
        const lang_code = e.target.value;
        i18n.changeLanguage(lang_code);
      };
    
    const [isOpen, setIsOpen] = useState(false);
    return (
        <header className="nav">
           <nav className="bg-white dark:bg-gray-900 fixed w-full z-20 top-0 start-0 border-b border-gray-200 dark:border-gray-600 shadow-md">
                <div className="2xl:max-w-screen-2xl xl:max-w-screen-xl lgC:max-w-screen-lg-custom mdC:max-w-screen-md-custom  flex flex-wrap items-center justify-between mx-auto p-2">
                    <div className="flex items-center">
                        <SwitchSpot />
                    </div>
                    <div className="flex md:order-2 space-x-3 md:space-x-0 rtl:space-x-reverse">
                        <select defaultValue={i18n.language} onChange={onChangeLang} 
                            className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                            aria-label="Select your country"
                            autoComplete="en"
                            name="country"
                        >
                            {LANGUAGES.map(({ code, label }) => (
                            <option key={code} value={code}>
                                {label}
                            </option>
                            ))}
                        </select>
                        
                        <button 
                            data-collapse-toggle="navbar-sticky"   
                            type="button" 
                            className="inline-flex items-center p-2 w-10 h-10 justify-center text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600" aria-controls="navbar-sticky" aria-expanded="false"
                            onClick={() => setIsOpen(!isOpen)}
                        >
                            <span className="sr-only">Open main menu</span>
                            <svg className="w-5 h-5" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 17 14">
                                <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M1 1h15M1 7h15M1 13h15"/>
                            </svg>
                        </button>
                    </div>
                    <div className={`${isOpen ? '' : 'hidden'} md:flex space-x-4 items-center justify-between  w-full md:flex md:w-auto md:order-1`} id="navbar-sticky">
                        <ul className="flex flex-col p-4 md:p-0 mt-4 font-medium border border-gray-100 rounded-lg bg-gray-50 md:space-x-8 rtl:space-x-reverse md:flex-row md:mt-0 md:border-0 md:bg-white dark:bg-gray-800 md:dark:bg-gray-900 dark:border-gray-700 dark:text-white">
                            <li>
                                <Link activeClass="active" smooth spy to="about"  href="#">
                                    {t('header.title1')}
                                </Link>
                            </li>
                            <li>
                                <Link activeClass="active" smooth spy to="projects" href="#">
                                    {t('header.title2')}
                                </Link>
                            </li>
                            <li>
                                <Link activeClass="active" smooth spy to="certifications" href="#">
                                    {t('header.title3')}
                                </Link>
                            </li>
                            <li>
                                <Link activeClass="active" smooth spy to="contact" href="#">
                                    {t('header.title4')}
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </header>
    )
}

export default Header;