import * as React from "react"
import type { HeadFC, PageProps } from "gatsby"
import GridCertification from '../components/GridCertification'
import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import englishContent from "../lang/en.json";
import spanishContent from "../lang/es.json";
import Header from '../components/Header';
import Hero from '../components/Hero';
import ContactForm from '../components/ContactForm';
import CardProjects from "../components/CardProjects";
import salesfoces from "../images/salesfoces.jpg";


i18n.use(initReactI18next).init({
  resources: {
      en: englishContent,
      es: spanishContent,
  },

  fallbackLng: "en",
  interpolation: {
      escapeValue: false
  },
});


const IndexPage: React.FC<PageProps> = () => {
  return (
    <main>
          <div  className='container-scroll'>    
            <Header />
            <section className="App flex mx-auto py-20 mb-2" id="about" >
                  <Hero />
            </section>
            <section className="App flex mx-auto py-20 mb-2" id="projects" >
                  <CardProjects />
            </section>
            <section className="App flex mx-auto py-20 mb-10 md:mb-2" id="certifications" >
                  <GridCertification />
            </section>
            <section className="App flex mx-auto sm:pt-72 sm:pb-36 md:pt-28 md:pb-96 pt-32 pb-32" id="contact" >
                  <ContactForm />
            </section>
          </div>
    </main>
  )
}

export default IndexPage

export const Head: HeadFC = () => {
    return (
    <>
        <html lang="en" />
        <title>Expert Salesforce B2C Developer | HubSpot, React, PHP Specialist</title>
        <link rel="manifest" href="/manifest.json" />
          <meta 
            name="description" 
            content="Certified Salesforce B2C developer with expertise in HubSpot, React, and PHP. Available for freelance projects and contract work to build, integrate, and optimize web solutions." 
          />
          <meta name="robots" content="index, follow" />
          
          <meta property="og:title" content="Expert Salesforce B2C Developer | HubSpot, React, PHP Specialist" />
          <meta 
            property="og:description" 
            content="Offering custom development services in Salesforce B2C, HubSpot, React, and PHP. Contact me for your next web project." 
          />
          <meta property="og:url" content="https://h790.github.io/" />
          <meta property="og:image" content={salesfoces} />
          <meta property="og:type" content="website" />
          <meta property="og:site_name" content="Hugo Kevin | Salesforce B2C Developer" />
          <meta name="twitter:card" content="summary_large_image" />
          <meta name="twitter:title" content="Salesforce B2C Developer | HubSpot, React, PHP Specialist" />
          <meta 
            name="twitter:description" 
            content="Experienced developer in Salesforce B2C, HubSpot, React, and PHP. Let's work together on your next project." 
          />
          <meta name="twitter:image" content={salesfoces} />
          <meta name="twitter:site" content="@HK72232150" />
          <meta name="twitter:creator" content="@HK72232150" />
    </>
  );
}
