import React from 'react';
import { CertificateDetails } from '../model/Certification';

const ShowCertification: React.FC<CertificateDetails> = (props)=> {
    
    return (
        <a href={props.url}>
            <img src={props.img} alt={props.alt}  className='jello-vertical drop-shadow-light dark:drop-shadow-dark' width="100%" height="100%" loading="eager"/>
        </a>
    )
}
export default ShowCertification