import React from "react";
import ShowCertification from "./ShowCertification";
import { ListCert } from "../model/Certification";
import { useTranslation } from "react-i18next";
import { Flip  } from "react-awesome-reveal";

const GridCertification = ()=> {
    const { t } = useTranslation();
    return (
        <>
            <div className="grid grid-cols-2 md:grid-cols-4 grid-rows-auto gap-2  justify-items-center md:w-full">
                <h1 className="text-5xl font-bold md:col-span-4 col-span-2 dark:text-white">{t("cert.title")}</h1>
                {Object.entries(ListCert).map(([key, { url, alt, img }], index) => (
                    <div key={key} className={index === 0 ? 'md:col-span-4 col-span-2' : index > 2  ? 'md:col-span-1 col-span-2 ' :'md:col-span-2 col-span-1'}>
                        <Flip  cascade={true} damping={0.1} triggerOnce={true}>
                            <ShowCertification alt={alt} img={img} url={url} />
                        </Flip>
                    </div>
                ))}
            </div>
        </>
    )
}

export default GridCertification