import React from "react";
import { ProjectsDetails } from '../model/Projects';
import { useTranslation } from "react-i18next";

const CardProject: React.FC<ProjectsDetails> = (props)=> {
    const { t } = useTranslation();
    return (
        <div className="gap-5 m-4">
            <img 
                className={"float-left object-cover rounded-lg relative aspect-w-4 aspect-h-5 sm:aspect-w-16 sm:aspect-h-9 mr-4 "+ props.classCustom || ''} 
                src={props.img} alt={t(props.alt) || ''} style={props.css || {}}
                width={props.width || 'auto'}
                height={props.height || 'auto'}
                loading="eager"
                />
            <p>{t(props.text)}</p>
            <a href={props.url} className="text-blue-800 hover:text-blue-900 hover:underline transition duration-300 dark:text-blue-300 dark:hover:text-blue-400">{t(props.textLink)}</a>
        </div>
    )
}

export default CardProject;