import React, { useRef, useState  } from 'react';
import emailjs from 'emailjs-com';
import { useTranslation } from "react-i18next";
import AlertFail, {AlertSuccess}  from './Alert';
import { Bounce } from "react-awesome-reveal";

const SERVICE_ID = process.env.REACT_APP_SERVICE_ID || '';
const TEMPLATE_ID = process.env.REACT_APP_TEMPLATE_ID || '';
const USER_ID = process.env.REACT_APP_USER_ID || '1';

const ContactForm = () => {
  const { t } = useTranslation();
  const form = useRef<HTMLFormElement | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [alertType, setAlertType] = useState('');

  const sendEmail = (e: { preventDefault: () => void; }) => {
    e.preventDefault();
    setIsSubmitting(true)
    emailjs.sendForm(
      SERVICE_ID,
      TEMPLATE_ID, 
      form.current || '',
      USER_ID 
    )
    .then((result) => {
        setAlertType("success");
        timeMessage();
    }, (error) => {
        setAlertType("fail");
        timeMessage();
    });
  };
  const timeMessage =()=> {
    setTimeout(() => {
      setAlertType('');
    }, 100000);
  }

  return (
    <div className="py-8 lg:py-16 px-4  max-w-screen-md  md:max-w-screen-sm sm:mx-auto m-4 bg-white rounded-[23px] shadow-md dark:bg-gray-800 dark:text-white dark:shadow-[0px_4px_6px_rgba(255,255,255,0.1)]"> 
    <Bounce cascade={true} damping={0.1} triggerOnce={true}>
        <h2 className="mb-4 text-4xl tracking-tight font-extrabold text-center text-gray-900 dark:text-white text-left">{t('contact.title')}</h2>
        <p className={"font-light text-center text-gray-500 dark:text-gray-400 sm:text-xl"+ alertType !== '' ?  '' :'mb-8 lg:mb-16'}>{t('contact.body')}</p>
        {alertType === "success" && (
          <>
            <AlertSuccess />
          </>
        )}
        {alertType === "fail" && (
          <>
            <AlertFail />
          </>
        )}
        <form ref={form} onSubmit={sendEmail} className="space-y-8 text-left">
            <div>
                <label htmlFor="from_name" className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300 text-left">{t('contact.label1')}</label>
                <input type="text" name="from_name" id="from_name" className="block p-3 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 shadow-sm focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500 dark:shadow-sm-light" />
            </div>
            <div>
                <label htmlFor="user_email" className='dark:text-gray-300'>{t('contact.label2')}</label>
                <input type="email" name="user_email" id="user_email" className="shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-primary-500 focus:border-primary-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500 dark:shadow-sm-light" />
            </div>
            <div className="sm:col-span-2">
                <label htmlFor="message" className='dark:text-gray-300'>{t('contact.label3')}</label>
                <textarea name="message" id="message" className="block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg shadow-sm border border-gray-300 focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500" />
            </div>
            <button 
              type="submit" 
              className="py-3 px-5 text-sm font-medium text-center text-white rounded-lg bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-primary-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-primary-800 w-full"
              disabled={isSubmitting}
            >
                {t('contact.button')}</button>
        </form>
        </Bounce>
    </div>
  );
};

export default ContactForm;